import { useEffect } from 'react';
import { Card, Form, Input, InputNumber, Button, notification } from 'antd';
import { SaveOutlined } from '@ant-design/icons';
import http from '../services/http';

const SemrushSettingForm = () => {
    const [form] = Form.useForm();
    useEffect(() => {
        http.get("/semrush/setting").then(({ data }) => {
            form.setFieldsValue({
                ...data,
                membershipLids: data.membershipLids.join(',')
            });
        });
    }, []);
    const onFinish = (data) => {
        http.post("/semrush/setting", data)
        .then(() => {
            notification.success({ message: "Update config successfully." });
        })
        .catch((err) => {
            notification.error({
                message: err.response.data
            });
        });
    }

    return (
        <Card title="Settings" style={{ marginBottom: 15 }}>
            <Form
                name="settings"
                layout="vertical"
                onFinish={onFinish}
                autoComplete="off"
                form={form}
            >
                <Form.Item
                    label="Domain Overview Per Day Limit"
                    name="domainOverviewLimit"
                    rules={[{ required: true, message: 'Please put down the domain overview per day limit.' }]}
                >
                    <InputNumber style={{ width: '100%' }}/>
                </Form.Item>
                <Form.Item
                    label="Keyword Overview Per Day Limit"
                    name="keywordOverviewLimit"
                    rules={[{ required: true, message: 'Please put down the keyword overview per day limit.' }]}
                >
                    <InputNumber style={{ width: '100%' }}/>
                </Form.Item>
                <Form.Item
                    label="Memebership LIDs"
                    name="membershipLids"
                    rules={[{ required: true, message: 'Please put down memebershp lids.' }]}
                >
                    <Input/>
                </Form.Item>
                <Form.Item
                    label="Membership API Key"
                    name="membershipApiKey"
                    rules={[{ required: true, message: 'Please put down the membership api key.' }]}
                >
                    <Input/>
                </Form.Item>
                <Form.Item
                    label="User Agent"
                    name="userAgent"
                    rules={[{ required: false }]}
                >
                    <Input/>
                </Form.Item>
                <Form.Item
                    label="Cookie"
                    name="cookie"
                    rules={[{ required: false }]}
                >
                    <Input/>
                </Form.Item>
                <Form.Item>
                    <Button style={{ float: 'right' }} type="primary" htmlType="submit"><SaveOutlined/> Submit</Button>
                </Form.Item>
            </Form>
        </Card>
    );
}
export default SemrushSettingForm;