import { Card, Form, Input, Button, notification } from 'antd';
import { LoginOutlined } from '@ant-design/icons';
import http from '../services/http';

const SemrushAuthForm = () => {
    const [form] = Form.useForm();
    const onFinish = (data) => {

        http.post('/semrush/login', data)
        .then(() => {
            notification.success({
                message: "Started session successfully."
            });
        })
        .catch((err) => {
            notification.error({
                message: err.response.data
            });
        });
    }

    return (
        <Card 
            title="Semrush Login Form"
        >
            <Form 
                name="settings"
                layout="vertical"
                onFinish={onFinish}
                autoComplete="off"
                form={form}
            >
                <Form.Item
                    label="E-Mail"
                    name="email"
                    rules={[{ required: true, message: 'Please put down your email.' }]}
                >
                    <Input type="email"/>
                </Form.Item>
                <Form.Item
                    label="Password"
                    name="password"
                    rules={[{ required: true, message: 'Please put down your password.' }]}
                >
                    <Input type="password"/>
                </Form.Item>
                <Form.Item style={{ float: 'right' }}>
                    <Button type="primary" htmlType="submit"><LoginOutlined/> Login</Button>
                </Form.Item>
            </Form>
        </Card>
    )
}
export default SemrushAuthForm;